.bloginput{
    width: 100%;
    border: 1px solid #ccc;
    text-align: center;
    padding: 25px;
}
.bloginput>h2{
    margin-bottom: 10px;
}
.bloginput input,
.bloginput textarea{
    width: 390px;
    padding: 10px;
    margin-bottom: 15px;
}
.bloginput textarea{
    height: 165px;
}
.bloginput select{
    padding: 10px;
    margin-bottom: 15px;
}
.bloginput button{
    width: 390px;
    padding: 10px;
    cursor: pointer;
}